import styled from "styled-components";

export const Container = styled.div`
    margin-top: var(--navHeight);
    background-color: #000;
    color: var(--color1);

    h2 {
        margin: auto;
        text-align: center;
        font-size: 3.5rem;
        font-family: 'Mynerve', cursive;
        font-weight: 600;
        padding: 20rem 5rem;
        max-width: 100rem;
        background-color: #000;

        :hover ~ .cursor {
            display: block;
            transform: scale(4);
        }
    }

    .cursor {
        display: none;
        width: 3rem;
        height: 3rem;
        background-color: var(--color1);
        position: fixed;
        border-radius: 50%;
        pointer-events: none;
        mix-blend-mode: difference;
    }

    img {
        transform: rotate(90deg);
        width: 4rem;
        padding-left: 4rem;
        padding-top: 1.5rem;
    }



    @media screen and (min-width: 900px) {
        h2 {
            font-size: 4.5rem;
            padding: 20rem;
        }
    }
`;