import { useEffect, useState } from "react";
import { Container } from "./styles";
import Light from '../../assets/light.png';

const Intro = () => {
    const [cursorX, setCursorX] = useState<number>();
    const [cursorY, setCursorY] = useState<number>();
    const [mouse, setMouse] = useState<boolean>(false);

    useEffect(() => {
        if(mouse === true) {
            window.addEventListener('mousemove', (e) => {
                setCursorX(e.pageX);
                setCursorY(e.pageY);
            });
        }
    });

    return(
        <Container
        >
            <h2
            onMouseEnter={() => setMouse(true)}
            onMouseLeave={() => setMouse(false)}
            
            >A nossa missão é ajudar você a aprender sobre a programação de uma forma descomplicada.</h2>
            <div className="cursor"
                style={{
                    left: cursorX + 'px',
                    top: cursorY + 'px'
                }}
            >
                <img src={Light} alt="imagem de uma lanterna" />
            </div>
        </Container>
    );
}

export default Intro;