import styled from "styled-components";

export const Container = styled.div`
    padding: 0 2rem;
    background-color: var(--headerColor);
    color: var(--color1);
    box-shadow: 0 0 20px 5px var(--headerColor);
    position: fixed;
    top: 0;
    width: 100%;
    box-sizing: border-box;
    z-index: 99;

    .headerTop {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: var(--navHeight);
    }

    .left {
        width: 13rem;

        img {
            width: 100%;
            cursor: pointer;
        }
    }

    .right {
        display: flex;
        align-items: center;
    }

    .menuIcon {
        width: 5rem;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;

        .icon {
            width: 4rem;
            height: 4rem;
            color: var(--color1);

            :hover {
                color: var(--color3);
            }
        }
    }

    .headerBottomWeb {
        display: none;
    }

    .headerBottomMobile {
        transition: ease-in-out .4s;
        overflow: hidden;
        display: flex;
        align-items: center;

        ul {
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            gap: 1.5rem;

            li {
                list-style: none;
                font-size: 2.2rem;
                width: 100%;
                cursor: pointer;
                
                .link {
                    color: var(--color1);
                    text-decoration: none;

                    :hover {
                        color: var(--color3);
                    }
                }
        
                .line {
                    background-color: var(--color3);
                    height: 1px;
                    width: 0;
                    transition: ease-in-out .5s;
                }
                
                :hover {        
                    .line {
                        width: 100%;
                    }
                }
            }
        }
    }

    @media screen and (min-width: 900px) {

        .all {
            max-width: 110rem;
            display: flex;
            justify-content: space-between;
            margin-inline: auto;
        }

        .right {
            display: none;
        }

        .headerBottomWeb {
            display: block;
        }
        ul {
            height: 100%;
            display: flex;
            flex-direction: row;
            align-items: center;
            gap: 1.5rem;

            li {
                list-style: none;
                font-size: 2.1rem;
                cursor: pointer;

                .link {
                    color: var(--color1);
                    text-decoration: none;

                    :hover {
                        color: var(--color3);
                    }
                }
        
                .line {
                    background-color: var(--color3);
                    height: 1px;
                    width: 0;
                    transition: ease-in-out .5s;
                }
                
                :hover {        
                    .line {
                        width: 100%;
                    }
                }
            }
        }
    }
`;