import { Container2 } from "./styles";
import { useState } from "react";
import JS1 from "./js1";
import ProjetoJs1 from "./projeto1";

const JSContent = () => {
    const [js1, setJs1] = useState(false);
    const [projeto1, setProjeto1] = useState(false);

    const show1 = () => {
        setJs1(true);
        setProjeto1(false);
    }
    const show2 = () => {
        setJs1(false);
        setProjeto1(true);
    }

    return(
        <Container2>
            <h2>Escolha um tema</h2>
            <ul>
                <li onClick={show1}>Introdução</li>
                <li onClick={show2}>Manipulando DOM 1</li>
            </ul>

            {js1 && <JS1 />}
            {projeto1 && <ProjetoJs1 />}
            
        </Container2>
    );
}

export default JSContent;