import styled from "styled-components";

export const Container = styled.div`
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    button {
        width: 10rem;
        height: 5rem;
        border-radius: 5rem;
        border: 1px solid var(--borderColor);
        background-color: var(--borderColor);
        color: var(--color2);
        font-size: 2rem;
        margin-bottom: 5rem;
        cursor: pointer;

        :hover {
            background-color: transparent;
        }
    }
`;