import { Container } from "./styles";
import HTML from '../../assets/html.png';
import CSS from '../../assets/css.png';
import JS from '../../assets/js.png';
import REACT from '../../assets/react.png';
import GITHUB from '../../assets/github.png';
import PENCIL from '../../assets/pencil.png';
import INTRO from '../../assets/computer.png';
import { Link } from "react-router-dom";

const Subjects = () => {
    return(
        <Container>
            <h2>Escolha um assunto</h2>
            <div className="subjects">
                <Link className="link" to='/introduce'>
                    <div className="subject">
                        <div className="square">
                            <img src={INTRO} alt="Desenho de um computador" />
                        </div>
                        <div className="text">
                            <h3>Introdução</h3>
                            <p>Aprenda sobre a programação</p>
                        </div>
                    </div>
                </Link>

                <Link className="link" to='/html'>
                    <div className="subject">
                        <div className="square">
                            <img src={HTML} alt="logo do HTML" />
                        </div>
                        <div className="text">
                            <h3>HTML</h3>
                            <p>Aprenda sobre o HTML</p>
                        </div>
                    </div>
                </Link>

                <Link className="link" to='/css'>
                    <div className="subject">
                        <div className="square">
                            <img src={CSS} alt="logo do CSS" />
                        </div>
                        <div className="text">
                            <h3>CSS</h3>
                            <p>Aprenda sobre o CSS</p>
                        </div>
                    </div>
                </Link>
                
                <Link className="link" to='javascript'>
                    <div className="subject">
                        <div className="square">
                            <img src={JS} alt="logo do Javascript" />
                        </div>
                        <div className="text">
                            <h3>Javascript</h3>
                            <p>Aprenda sobre o Javascript</p>
                        </div>
                    </div>
                </Link>

                <Link className="link" to='reactjs'>
                    <div className="subject">
                        <div className="square turn">
                            <img src={REACT} alt="logo do ReactJS" />
                        </div>
                        <div className="text">
                            <h3>ReactJS</h3>
                            <p>Aprenda sobre o ReactJS</p>
                        </div>
                    </div>
                </Link>

                {/* <Link className="link" to='github'>
                    <div className="subject">
                        <div className="square">
                            <img src={GITHUB} alt="logo do Github" />
                        </div>
                        <div className="text">
                            <h3>Github</h3>
                            <p>Aprenda sobre o Github</p>
                        </div>
                    </div>
                </Link> */}

                <Link className="link" to='exercicios'>
                    <div className="subject">
                        <div className="square">
                            <img src={PENCIL} alt="Imagem animada de uma caneta" />
                        </div>
                        <div className="text">
                            <h3>Exercícios</h3>
                            <p>Treine com alguns desafios</p>
                        </div>
                    </div>
                </Link>
            </div>
        </Container>
    );
}

export default Subjects;