import { Container } from "./style";
import React from 'react';
import emailjs from 'emailjs-com';

export const Email = () => {

    const sendEmail = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
    
        emailjs.sendForm('service_45p8grh', 'template_dv256s9', e.currentTarget, 'dawLGdpCYA1urYPOL')
          .then((result) => {
              console.log(result.text);
          }, (error) => {
              console.log(error.text);
          });

          e.currentTarget.reset();
      };

    return (
        <Container>
            <form onSubmit={sendEmail} id="contatos"  >
                <input type="text" name="name" placeholder="Digite seu nome" required />
                <input type="email" name="email" placeholder="Digite seu endereço de email" required />
                <textarea name="message" rows={7} placeholder="Digite sua mensagem" required ></textarea>
                <button type="submit" className="btn">Enviar</button>
            </form>
        </Container>
    );
}